          monthDay: "07-04",
          year: "2011"
        }
      ]
    };
  },
  created() {
    tools.menu();
  },
  methods: {
    pageTurn: function(name, val) {
      this.$router.push({ name: name, params: { title: val } });
    },
    prevInfo: function() {
      let end = this.partyConstructionData[
        this.partyConstructionData.length - 1
      ];
      this.partyConstructionData.unshift(end);
      this.partyConstructionData.pop();
    },
    nextInfo: function() {
      let start = this.partyConstructionData[0];
      this.partyConstructionData.push(start);
      this.partyConstructionData.shift();
    }
  },
  components: {
    myHeader: Header,
    myFooter: Footer
  }
};
